import "core-js/modules/es.array.push.js";
import { useStore } from 'vuex';
import router from '../router/index';
import LocalStorage from '../components/LocalStorage.vue';
import ToSearch from '../components/ToSearch.vue';
import RightBox from '../components/RightBox.vue';
import AppList from '../components/AppList.vue';
import $ from 'jquery';
import { reactive, toRefs, onMounted } from 'vue';
export default {
  components: {
    LocalStorage,
    ToSearch,
    RightBox,
    AppList
  },

  setup() {
    const store = useStore();
    const vue = reactive({
      hour: new Date().getHours(),
      min: new Date().getMinutes() < 10 ? "0" + new Date().getMinutes() : new Date().getMinutes()
    });
    onMounted(() => {
      let i = store.state.backImg || localStorage.getItem("color") || 6;
      $('html').css({
        '--backColor': store.state.colorList[i - 1].backColor
      });
      $('html').css({
        '--color': store.state.colorList[i - 1].color
      });
      store.commit("show", '');
      const jwt = localStorage.getItem("jwt");

      if (jwt) {
        store.commit("updateToken", jwt);
        store.dispatch("getinfo", {
          success() {
            router.push({
              name: "main"
            });
          },

          error() {}

        });
      }

      time;

      function time() {
        vue.hour = new Date().getHours();
        vue.min = new Date().getMinutes() < 10 ? "0" + new Date().getMinutes() : new Date().getMinutes();
      }

      setInterval(time, 1000);
    });
    return { ...toRefs(vue)
    };
  }

};