import axios from 'axios';
import { reactive, toRefs, watch } from 'vue';
export default {
  setup() {
    var index = -1;
    const vue = reactive({
      is_show: false,
      name: '',
      list: []
    });

    function showed() {
      setTimeout(function () {
        vue.is_show = false;
      }, 100);
    }

    function hh() {
      vue.is_show = true;
      setTimeout(function () {
        axios.get(`apis/su?wd=${encodeURI(vue.name)}`).then(response => {
          vue.list = [];
          if (vue.name === ' ') return;
          if (response.data === ' ') return;
          var st;
          var en;
          let h = response.data;

          for (let i = 0; i < h.length; i++) {
            if (h[i] === '[') st = i;
            if (h[i] === '}') en = i;
          }

          h = h.slice(st, en);
          vue.list = JSON.parse(h);
        }, error => {
          console.log("", error.message);
        });
      }, 150);
    }

    function click(i) {
      window.open(`https://www.baidu.com/s?word=${encodeURI(i)}`);
      vue.list = [];
      vue.name = '';
    }

    watch(() => vue.name, () => {
      hh();
    });

    function choise(e) {
      if (e.keyCode === 37 || e.keyCode === 39 || e.keyCode === 8) index = -1;
      let ul = document.getElementsByTagName('ul')[0];

      if (e.keyCode === 38) {
        //
        if (index !== -1) ul.children[index].style.backgroundColor = '';
        if (index <= 0) index = vue.list.length - 1;else index--;
        document.getElementsByTagName('input')[0].value = vue.list[index];
        ul.children[index].style.backgroundColor = '#7f7f7f3a';
      }

      if (e.keyCode === 40) {
        //
        if (index !== -1) ul.children[index].style.backgroundColor = '';
        if (vue.list.length < index + 2) index = 0;else index++;
        document.getElementsByTagName('input')[0].value = vue.list[index];
        ul.children[index].style.backgroundColor = '#7f7f7f3a';
      }
    }

    return { ...toRefs(vue),
      hh,
      click,
      choise,
      showed
    };
  }

};