import "core-js/modules/es.array.push.js";
import { mapState, mapActions } from 'vuex';
import { ElMessage } from 'element-plus';
import { useStore } from 'vuex';
import router from '../router/index';
import login from '../views/AccountLogin';
import register from '../views/AccountRegister.vue';
import $ from 'jquery';
import { onMounted, ref } from 'vue';
export default {
  components: {
    login,
    register
  },

  setup() {
    const List = ref([]);
    const other = ref('');
    const store = useStore();

    const jiazai = () => {
      $.ajax({
        url: 'https://so.beink.cn/user/admin/backlist/show/',
        type: 'post',

        success(res) {
          List.value = JSON.parse(res);
        },

        error(res) {
          console.log(res);
        }

      });
    };

    const aa = () => {
      $.ajax({
        url: 'https://so.beink.cn/user/admin/backlist/save/',
        type: 'post',
        headers: {
          Authorization: "Bearer " + store.state.token
        },
        data: {
          backlist: JSON.stringify(List.value)
        }
      });
    };

    onMounted(() => {
      jiazai();
    });

    function addSrc() {
      List.value.push(other.value);
      other.value = '';
      aa();
    }

    const ddd = i => {
      List.value = List.value.filter(tab => tab !== i);
      aa();
    };

    return {
      List,
      ddd,
      addSrc,
      other,
      jiazai
    };
  },

  data() {
    return {
      dialogLogin: false,
      dialogRegister: false,
      drawer: false,
      dialogBack: false,
      visible: false,
      labelPosition: 'right',
      store: useStore()
    };
  },

  computed: { ...mapState(['background', 'list', 'colorList'])
  },

  mounted() {
    let hour = new Date().getHours();
    let Time = document.getElementById("time");
    if (hour < 18 && hour >= 6) Time.src = require('@/assets/sun.png');else Time.src = require('@/assets/moon.png');
    document.getElementsByTagName('body')[0].style.backgroundImage = `url("${this.background}")`;
    let i = this.store.state.backImg || localStorage.getItem("color") || 6;
    $('html').css({
      '--backColor': this.colorList[i - 1].backColor
    });
    $('html').css({
      '--color': this.colorList[i - 1].color
    });
  },

  methods: { ...mapActions(['changeBackground']),

    open1() {
      ElMessage('');
    },

    logout() {
      sessionStorage.setItem("name", "");
      this.store.dispatch("logout");
      this.open1();
    },

    send(i) {
      this.$bus.emit('send', i);
      router.push({
        name: 'study'
      });
    },

    change(a, b, c) {
      if (this.store.state.background !== '') return;

      if (this.store.state.is_login === true) {
        $.ajax({
          url: 'https://so.beink.cn/user/account/color/',
          type: 'post',
          headers: {
            Authorization: "Bearer " + this.store.state.token
          },
          data: {
            account: this.store.state.account,
            backImg: c,
            list: localStorage.getItem("list")
          }
        });
      }

      localStorage.setItem("color", c);
      $('html').css({
        '--backColor': a
      });
      $('html').css({
        '--color': b
      });
    }

  }
};