import { nanoid } from 'nanoid';
import { mapState, mapMutations } from 'vuex';
export default {
  data() {
    return {
      href: '',
      name: '',
      isShow: false,
      icon: ['#icon-yongquan', '#icon-zhifeiji', '#icon-qiqiu', '#icon-zhifengche', '#icon-fengche', '#icon-quqi', '#icon-xigua', '#icon-yinliao', '#icon-bingqilin', '#icon-diqiuyi']
    };
  },

  computed: { ...mapState(['localList'])
  },
  methods: {
    hh() {
      if (this.href === '' || this.name === '') {
        alert('');
        return;
      }

      const ll = {
        id: nanoid(),
        href: this.href,
        name: this.name,
        icon: `${this.icon[Math.floor(Math.random() * 10)]}`
      };
      this.$store.dispatch('changeList', ll);
      this.href = this.name = '';
      this.isShow = !this.isShow;
    },

    nohh() {
      this.isShow = !this.isShow;
      this.href = this.name = '';
    },

    ...mapMutations(['dele'])
  }
};